
*{
  margin:0px;
  padding:0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.toubu{
  height: 45px;
  width:100%; 
  background-color:blueviolet;
  line-height: 45px;
  color:#fff;
}
a{
  text-decoration:none;
}
.toubu span{
  color:#fff
}
#root{
  color:chartreuse;
}
.router-view {
  height: 200px;
  width: 100%;
  background-color: aqua;
}
.router-link-exact-active span, .router-link-active span{
  color:#fff;
}
p{
  color:brown;
  font-size:30px;
  line-height: 200px;
}
.nextbut{
  height: 30px;
  width:100px;
  background-color:cornflowerblue;
  -webkit-box-shadow:1px 1px 5px 2px rgba(0,0,0,0.3);
  margin:0 auto;
  border-radius:3px;
  margin-top:20px;
  color:#fff;
  line-height: 30px;
  cursor: pointer;
}
.nextbut:active{
   -webkit-box-shadow:1px 1px 1px 1px rgba(0,0,0,0.1);
}



